import Vue from 'vue'
import Router from 'vue-router'
// 工单打卡
import udenglu from '@/view/worker/workers/ulogin'
import ucenter from '@/view/worker/workers/ucenter'
import ulist from '@/view/worker/workers/daka'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'udenglu',
      component: udenglu
      // redirect: 'Home'
    },
    {
      path: '/worker/ulogin',
      name: 'udenglu',
      component: udenglu
    },
    {
      path: '/worker/ucenter',
      name: 'ucenter',
      component: ucenter
    },
    {
      path: '/worker/ulist',
      name: 'ulist',
      component: ulist
    }
  ]
})



